import React, { Component } from 'react'
import "../assets/css/sidebar.css";

class MenuCartList extends Component {
    render() {

        let { title, price, image, description } = this.props;
        console.log('ogutaq ', price);
        console.log('image ', image);
        return (
            <div className="data" >
                <li style={{ "justify-content": "center" }}>
                    <img src={image} style={{ "width": "150px", "height": "100px" }} />
                    <span style={{ "color": "red", "fontSize": "28px", "fontWeight": "bold" }}>  <i
                        onClick={{}}
                        className="fa fa-trash"
                    />
                          &nbsp;&nbsp;Hapus
                    </span>
                </li>
                <li style={{ "fontSize": "16px", "lineSpacing": "2px !important" }}>
                    <span>
                        {title}
                    </span>
                </li>
                <li><span className="gaji">$ {price}</span></li>
                {/* <li style={{ "fontSize": "14px", "maxHeight": "120px", "overflow": "hidden" }}>
                    Description: {description}
                </li> */}
            </div>
        )
    }
}
export default MenuCartList